<template>
    <div class="login-view">
        <span class="pic-1"></span>
        <span class="pic-2"></span>
        <span class="pic-3"></span>
        <div class="login-box">
            <el-form class="login-form" :model="params" status-icon auto-complete="on" :rules="roles" ref="form"
                     @keyup.native.enter="submit">
                <h1>管理系统</h1>
                <el-form-item prop="mobile">
                    <el-input v-model="params.mobile" size="medium" placeholder="账号"></el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input v-model="params.password" size="medium" type="password" placeholder="密码"></el-input>
                </el-form-item>
                <el-form-item class="submit-btn-box">
                    <el-button
                            :loading="sending"
                            type="primary"
                            size="medium"
                            @click="submit"
                            class="submit-btn">
                        立即登录
                    </el-button>
                </el-form-item>
                <p ref="shakeEle" class="login-error" v-show="error.status">{{error.message}}</p>
            </el-form>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import {apiLogin} from '@/api/index';
    import {logic, setStorage, createAsyncRouter, handleError, addClass, removeClass, hasClass} from '@/utils/index';
    import {mapMutations} from 'vuex'

    export default {
        name: 'login',
        data() {
            return {
                sending: false,
                error: {
                    status: false,
                    message: ''
                },
                params: {
                    mobile: '',
                    password: ''
                },
                roles: {
                    mobile: [
                        {required: true, message: '账号不能为空', trigger: 'blur'}
                    ],
                    password: [
                        {required: true, message: '密码不能为空', trigger: 'blur'}
                    ]
                },
                userInfo: null
            }
        },
        methods: {
            ...mapMutations([
                'SET_USERINFO',
                'SET_ASYNCROUTER'
            ]),
            shakeTips() {
                /**
                 * 添加按钮摇晃动画
                 */
                let shakeEle = this.$refs['shakeEle'];
                addClass(shakeEle, 'shake');
                /**
                 * 谷歌等现代浏览器
                 */
                shakeEle.addEventListener('webkitAnimationEnd', function (event) {
                    if (hasClass(shakeEle, 'shake')) {
                        removeClass(shakeEle, 'shake');
                    }
                });
                /**
                 * 标准语法
                 */
                shakeEle.addEventListener('animationend', function (event) {
                    if (hasClass(shakeEle, 'shake')) {
                        removeClass(shakeEle, 'shake');
                    }
                });
            },
            submit() {
                // 完成表单验证
                this.$refs['form'].validate(async (valid) => {
                    if (valid) {
                        this.sending = true;
                        /**
                         * 执行登录接口
                         */
                        await apiLogin(this.params)
                            .then(res => {
                                /**
                                 * 判断接口逻辑
                                 */
                                logic(res, result => {
                                        this.userInfo = result;
                                        this.error.status = false;

                                        // 判断是否具有登录权限
                                        if (!result['accessKeys'].length) {
                                            this.$notify({
                                                title: '提示',
                                                message: '该账号未分配菜单权限',
                                                type: 'warning',
                                                showClose: false
                                            });
                                            return false;
                                        }
                                        //生成router列表
                                        const asyncRouter = createAsyncRouter(result['accessKeys']);
                                        // 加入路由
                                        this.$router.addRoutes(asyncRouter);
                                        /**
                                         *  vuex 保存路由
                                         */
                                        this.SET_ASYNCROUTER(asyncRouter);
                                        /**
                                         *  session、vuex保存用户基本信息
                                         */
                                        this.SET_USERINFO(this.userInfo);
                                        // 跳转到系统首页
                                        this.$router.push({path: asyncRouter[0].path});
                                    },
                                    msg => {
                                        this.error = {
                                            status: true,
                                            message: msg
                                        };
                                        this.shakeTips();
                                    });
                            })
                            .catch(err => {
                                handleError(err);
                            })
                            .finally(() => {
                                this.sending = false;
                            });
                    }
                });
            }
        }
    }
</script>

